.d-flex{
    display: flex;
}

.space-around{
    justify-content: space-around;
}
.row{
    flex-direction: row;
}
.column{
    flex-direction: column;
}
.j-center{
    justify-content: center;
}
.a-center{
    align-items: center;
}
.wrap{
    flex-wrap: wrap;
}
.no-wrap{
    flex-wrap: nowrap;
}
.absolute{
    position: absolute;
}
.relative{
    position: relative;
}
.back{
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 800px;
}
.title_category{
    color: white;
    font-weight: 300;
    font-size: 64px;
    margin-bottom: 50px;
}
.gap{
    gap: 60px;
}
p{
    text-align: center;
    font-size: 24px;
}
button{
margin-top: 50px;
box-sizing: border-box;
display: flex;
justify-content: center;
align-items: center;
width: 316px;
height: 80px;
border: 3px solid #FFCB8E;
background-color:transparent;
color:#FFCB8E;
font-size: 24px;
font-weight: 600;
transition: all 500ms ease; 

}
button:hover {
	background: #FFCB8E(0,0,0,0);
	color:white;
	box-shadow: inset 0 0 0 3px #FFCB8E;
}
