.footer_wrapper{
    margin-bottom: 50px;
    background: linear-gradient( rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.9)),url("/src/components/header/image/background.png");
    background-repeat: no-repeat;
    background-size: cover;
}
.footer_wrapper h2 ,.footer_wrapper p{
    color:#FFCB8E;
    font-size: 24px;
    line-height: 30px;
    letter-spacing: 0.24em;
}
.info_footer{
    gap:50px;
    margin: 100px 0px 0px 0px;
}
.info_footer li{
    width: 300px;
}
.info_footer span{
   color:white;
}
.img_footer{
    gap:20px;
}
.social{
    justify-content: space-between;
    margin: 0px 40px 0px 40px;
}

@media(max-width:815px){
    .social{
        justify-content: center;
    }
}
.footer_wrapper span{
    color:white;
}