.review_form{
    gap:50px;
}
.review_form ul{
    width: 100%;
    align-items:flex-start;
    column-gap: 50px;
}

.review_item{
    width: 400px;
    justify-content: space-around;
}
.review_description{
    height: 450px;
}
.review_description p{
    text-align: start;
}

.line{
    margin:0px auto;
    width:100%;
    height: 3px;
    background-color:#FFCB8E;
    opacity: 70%;
    
}

@media (max-width:600px) {
    .review_item{
    width: 200px;
    height: 350px;
}
    .review_item p{
        font-size:15px;
    }
}